#header-tags{
  display: flex;
  font-size:1.5rem; 
  font-weight: bold; 

  li{
    display: flex;
    list-style: none;
    padding:0 10px;
    
    div{
      display: flex;
      padding: 0 5px; 
    }
  }
  ul{
    display: flex; 
    padding:0; 
    margin: 0;
  }
}


